import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    item: {}
  },
  mutations: {
    selectTableItem (state, row) {
      state.item = row
      console.log('Vuex current item:', state.item)
    },
    fromReqData (state) {

    }
  }
})

export default store



